import React, { Component } from 'react';
import './fibonacci.css';

class Fibonacci extends Component {
    constructor(props) {
        super(props)
        this.state = {
            fibonacciArray: ["0", "1/2", "1", "2", "3", "5", "8", "13", "21", "34", "55", "?"],
            toggle: true,
            selectedCard: ''
        }
        this.toggleCard = this.toggleCard.bind(this);
    }


    componentDidMount() {
        // let fibonacciArray = [];
        // fibonacciArray[0] = 0;
        // fibonacciArray[1] = 1;
        // for (let i = 2; i <= 9; i++) {
        //     var valueForArray = fibonacciArray[i - 2] + fibonacciArray[i - 1];
        //     fibonacciArray.push(valueForArray.toString())
        // }
        // fibonacciArray[1] = fibonacciArray[1] - 0.5;
        // this.setState({ fibonacciArray: fibonacciArray });
    }
    toggleCard = (item, selectedMain) => {
        if(selectedMain){
            this.setState({ selectedCard: item.toString() })
        }       
        this.setState({ toggle: !this.state.toggle })
    }
    render() {
        return (
            <div className="container">
                <div className="flip">
                    <div className={this.state.toggle ? 'card ' : 'card flipped'}>
                        <div className="face front">
                            <div className="row inner">
                                {this.state.fibonacciArray.map(item => (
                                    <div className="col-xs-4" key={item}>
                                        <a onClick={this.toggleCard.bind(this, item, 'Main')} href="#" className="btn btn-sq">
                                            {item}</a></div>))}
                            </div>
                        </div>
                        <div className="face back" onClick={this.toggleCard}>
                            <div className="row inner">
                                <div className="col-xs-12">
                                    <a onClick={this.toggleCard} href="#" className="btn btn-sq-lg">
                                        {this.state.selectedCard}</a></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Fibonacci;