import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import RaisedButton from 'material-ui/RaisedButton';
import TextField from 'material-ui/TextField';
import { fromJSON } from 'tough-cookie';
import { white } from 'material-ui/styles/colors';
class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      password: ''
    }
  }
  render() {
    return (
      <MuiThemeProvider>
        <div>
          {/* <AppBar
             title="Login"
           /> */}
          <TextField floatingLabelStyle={style.floatingLabelFocusStyle}
            underlineFocusStyle={style.underlineFocusStyle}
            hintText="Enter your Username"
            floatingLabelText="Username"
            onChange={(event, newValue) => this.setState({ username: newValue })}
          />
          <br />
          <TextField floatingLabelStyle={style.floatingLabelFocusStyle}
            type="password"
            hintText="Enter your Password"
            floatingLabelText="Password"
            onChange={(event, newValue) => this.setState({ password: newValue })}
          />
          <br />
          <RaisedButton className="style.floatingLabelFocusStyle" label="Submit" style={style} onClick={(event) => this.handleClick(event)} />
        </div>
      </MuiThemeProvider>
    );
  }
}
const style = {
  margin: 15,
  underlineFocusStyle: {
    "background-color": "white",
    color: "white"
  },
  floatingLabelFocusStyle: {
    color: "#fb0"
  }
};

export default Login;